import Vue from'vue'
import moment from 'moment'
import uniBadge from '@/components/uni-badge/uni-badge.vue'
import tabarNav from '@/components/tabar/tabar.vue'
export default {
	props: {
		listData: Array
	},
	data() {
		return {
			data: [],
			badgeColor: ''
		}
	},
	created() {
		
	},
	filters:{
		formatDate(time){
			let _time,data;
			if(time){
				 _time = time.split('T');
				 data = moment( _time[0]).locale('zh-cn').fromNow();
			}
			
			return data
			// console.log(self.$formatTime.timeAgo(time))
			// self.$formatTime.timeAgo(time)
		}
	},
	watch: {
		/**
		 *  解决异步prosp取值问题
		 */
		listData(newValue, oldValue) {
			let _data = this.listData;
			let self = this;
			setTimeout(() => {
				self.data = _data.map((item, index, arr) => {
					if (item.tab == "share") {
						item.tab = "分享"
						self.badgeColor = "error"
					} else if (item.tab == "ask") {
						item.tab = "问答"
						self.badgeColor = "success"
					} else if (item.tab == "job") {
						item.tab = "招聘"
						self.badgeColor = "warning"
					} else if (item.tab == "weex") {
						item.tab = "weex"
						self.badgeColor = "primary"
					}
					return arr[index] = item;
				})
				// console.log(this.data)
			},0)
		}
	},
	components: {
		tabarNav,
		uniBadge
	},

}
